#logo path {
	stroke-width: 2;
}

#logo path:nth-child(1),
#logo path:nth-child(2),
#logo path:nth-child(3) {
	animation: filly 3s ease forwards;
}
#logo path:nth-child(4),
#logo path:nth-child(5),
#logo path:nth-child(6) {
	animation: fillb 3s ease forwards;
}

#logo path:nth-child(1) {
	stroke-dasharray: 281;
	stroke-dashoffset: 281;
}

#logo path:nth-child(2) {
	stroke-dasharray: 270;
	stroke-dashoffset: 270;
}

#logo path:nth-child(3) {
	stroke-dasharray: 367;
	stroke-dashoffset: 367;
}
#logo path:nth-child(4) {
	stroke-dasharray: 403;
	stroke-dashoffset: 403;
}
#logo path:nth-child(5) {
	stroke-dasharray: 164;
	stroke-dashoffset: 164;
}
#logo path:nth-child(6) {
	stroke-dasharray: 206;
	stroke-dashoffset: 206;
}

@keyframes filly {
	from {
	}
	90% {
		stroke-dashoffset: 0;
		fill: transparent;
	}
	to {
		stroke-dashoffset: 0;
		fill: #fb0;
	}
}
@keyframes fillb {
	from {
	}
	90% {
		stroke-dashoffset: 0;
		fill: transparent;
	}
	to {
		stroke-dashoffset: 0;
		fill: black;
	}
}
